	module.exports = {"data":{"markdownRemark":{"html":"<p>If I want to move my hand, I can just decide to move it.</p>\n<p>Nothing stops me from doing so. *<em>moves hand</em>* See?</p>\n<p>But the question is, did I move my hand because I chose and decided to? Can my conscious thought <strong>drive</strong> this action?</p>\n<h3>Defining the question</h3>\n<p>Let's start at the top. What <em>is</em> consciousness? The first definition offered by a google search is \"a state of being aware of and responsive to one's surroundings\". Two ideas are being offered here: <em>perception</em>, and <em>responsiveness</em>.</p>\n<p>Sitting here in my chair, I can hear a car driving past. The sound waves reach my ear drums and my brain registers the signal. It enters my short-term memory and I <em>know</em> it happens. I <em>perceive</em> it.</p>\n<p>But more than this. I perceive my own thoughts. I perceive myself weighing different options. I perceive my emotions, my sensations.</p>\n<p>My leg starts to get uncomfortable from being in the same position for too long. I sense the discomfort, feel that I should move it, and my brain sends the signal to my leg muscles to adjust its position. I <em>respond</em>.</p>\n<p>It's interesting that these two ideas are tied together in the definition, as if they are somehow <em>related</em>.</p>\n<p>And how about consciousness being a state? Is it a physical state? A mental state? What even <em>is</em> a mental state? It can't be a state of consciousness, of awareness and perception, or that would be a circular definition. Probably, what we mean by a mental state is a <em>physical representation</em> of one's nervous system and the chemicals, hormones and charged/discharged states of neurons in all the different sections of one's brain. This sounds strange, but is intuitive in some way: everything else has a physical representation in the world, so why should consciousness(and by extension, conscious thought) be any different?</p>\n<p>To put it all together, consciousness is the physical state of perceiving and responding.</p>\n<h3>Explaining the apparent conscious direction of thought</h3>\n<p>What about conscious thought? It appears, that I am able to direct my attention, my awareness towards different directions. I can think about my fingers pressing the keys, about my breath, or the itch on my cheek. Our earlier definition of consciousness already has an answer for this. The physical system of my nervous system and brain interact, together with the input sensory data through my eyes, ears, nerves, in deterministic physical processes to go from one \"mental state\" to the next.</p>\n<p>In other words, there was no other way I could have responded in that situation. I couldn't have <strong>not</strong> moved my hand.</p>\n<p>How could that be? <em>I thought about my hand, and decided to move it, and moved it. I've been doing this my whole life!</em> Well, correlation does not imply causation. The perception of thinking about my hand, of deciding to move it, and of subsequently of it moving, does not mean that my awareness caused my hand to move.</p>\n<p>For how could it? How can the immaterial affect the material? My perception is merely that, perception. The physical processes of my body are managed by the state of my neurons, my hormones. <em>I</em>(or the perception of <em>I</em>) am nothing but a passenger with a toy steering wheel in this car that is my mind. <em>I</em> am a projection on the monitor that merely reflects what is happening within the CPU.</p>\n<p>A more realistic picture of how my hand moved follows. My brain is wired to seek certain outcomes(exactly <em>which</em> outcomes is a topic for another treatise). One of those outcomes is to prove points. I was trying to make a point about conscious thought, and so my system moved my hand to prove the point, with the side-effect of my perception thinking that it was the one who did the job.</p>\n<h3>Introducing terms for clarity</h3>\n<p>From here on out, I will refer to our conscious mind and awareness, the passenger in the car as our <strong>consciousness</strong>, and the physical systems of our brains and nervous system, together with our sensory input, which actually cause our actions as our <strong>mental state</strong>. The consciousness is born from the mental state, as a side effect of perception for sensory input.</p>\n<h3>The consequences of determinism</h3>\n<ol>\n<li>Our consciousness has no agency in its experiences</li>\n<li>Our actions are purely a result of our mental state</li>\n</ol>\n<h4>The joy or tragedy of having no agency</h4>\n<p>Our mental states cause the actions which make our consciousness feel good. On the other hand, they also cause our oft humanly poor behavior, but our consciousness is the one which perceives all the suffering for it. For example, Tom's mental state might cause him to punch his boss and get fired, but his consciousness will perceive the sadness of losing his job, the guilt and the possible subsequent drop in quality of life from lost income.</p>\n<h4>Punishment and reward</h4>\n<p>Why should we punish someone if <em>they</em> never committed the crime? We should not.</p>\n<p>Any kind of punishment, however, will always incidentally wrongly punish the blameless consciousness, for it is born from the mental state, whose future behavior we are trying to manipulate through the punishment.</p>\n<h3>The paradox of a deterministic future</h3>\n<p>The paradox is that our thoughts have agency, our perception of our thoughts has no agency, but we cannot separate the two, as one is born from the other. So where do we go from here?</p>\n<p>Well, the good news is that by reading and thinking about this, your mental state has <em>already</em> begun to change. It is learning that <em>it</em> is the cause of your sufferings and your joys, and that it has to step up its game, because your consciousness can't do anything to help it. It is learning not to blame your consciousness for failures, not to praise your consciousness for successes, and to let your consciousness just <em>feel</em>, unbiasedly, un-afraidly, humbly.</p>\n<p>After all, if I want to move my hand, my mental state can just decide to move it.</p>","frontmatter":{"title":"On free will"}}},"pathContext":{"slug":"/on-free-will/"}}

